.wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
}

.formWrapper {
  width: 334px;
  height: 579px;
  border-radius: 14px;
  background: #fff;
  display: flex;
  flex-direction: column;
  padding: 24px 30px;
  align-items: center;
  position: relative;
}

.logoContainer {
  width: 42px;
  height: 42px;
  border-radius: 10px;
  background: #222525;
  display: flex;
  align-items: center;
  justify-content: center;
}

.formHeader {
  color: #222525;
  font-size: 20px;
  line-height: 26px;
  font-family: "Gilroy-semibold";
  margin: 0;
  margin-top: 24px;
}

.inputStyle {
  width: 276px;
  height: 43px;
  border-radius: 6px;
  background: #f5f5f5;
  border: none;
  padding: 16px;

  color: #677582;
  font-family: "Gilroy-medium";
  font-size: 16px;
}
input:focus {
  outline: none;
  box-shadow: none;
  border: none;
}

.formButton {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 6px;
  background: #222525;
  width: 276px;
  height: 43px;
  margin-top: 30px;
  cursor: pointer;

  color: #fff;
  font-size: 16px;
  font-family: "Gilroy-medium";
}

.noAccText {
  color: #a9acaf;
  font-size: 15px;
  font-family: "Gilroy-medium";
  margin-top: 16px;
}

.signInRef {
  color: #222525;
  font-size: 15px;
  font-family: "Gilroy-medium";
  text-decoration: none;
}

.footerContainer {
  position: absolute;
  bottom: 30px;
  left: 24px;
  text-align: left;
}

.footerHeader {
  color: #727883;
  font-size: 14px;
  font-family: "Gilroy-medium";
  margin: 0;
  cursor: pointer;
}

.bannerWrapper {
  height: 579px;
  width: 685px;
  border-radius: 14px;
  background: #222525;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
}

.bannerHeader {
  margin: 0;
  color: #fff;
  font-size: 39px;
  font-family: "Gilroy-bold";
  z-index: 2;
}

.bannerFooterText {
  color: #6f7e7e;
  font-size: 11px;
  font-family: "Gilroy-medium";
  position: absolute;
  bottom: 27px;
}

.bannerBg {
  position: absolute;
  z-index: 1;
}
